@charset "UTF-8";
body {
  font-family: "";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.article {
  margin-bottom: 1.875rem;
}

.article > img {
  width: 100%;
  margin: 0 auto;
}

.article > h1,
.article > h2,
.article > h3,
.article > h4,
.article > h5 {
  margin-bottom: 0.625rem;
}

.article > h1 {
  font-size: 1.5rem;
}

.article-pnlink {
  text-align: center;
}

.article-pnlink > a {
  padding: 0 0.625rem;
}

a.card {
  display: block;
  width: 100%;
  position: relative;
}
.card-figure {
  padding-top: 63%;
  overflow: hidden;
  position: relative;
}

.card-image {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

h2.card-title {
  font-size: 0.625rem;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  width: 100%;
}

ul.cards-gallery {
  display: flex;
  flex-wrap: wrap;
}
li.cards-gallery-list {
  flex-basis: 50%;
}
@media screen and (min-width: 768px) {
  li.cards-gallery-list {
    flex-basis: 25%;
  }
}

.container {
  max-width: 95%;
  width: 100%;
  background: rgb(248, 248, 255);
  margin: 0 auto;
  display: flex;
}

.footer {
  max-width: 95%;
  width: 100%;
  background: rgb(248, 248, 255);
  margin: 0 auto;
  text-align: center;
}

.header {
  max-width: 95%;
  width: 100%;
  background: rgb(248, 248, 255);
  margin: 0 auto;
  text-align: center;
  margin-bottom: 3.125rem;
}

.main {
  flex: 3;
}

.modal__overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  z-index: 2;
}

.modal__container {
  background: rgb(255, 255, 255);
  padding: 1.875rem;
  margin: 0 0.625rem;
  width: 50%;
  overflow-y: auto;
}

.modal__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal__header .modal__close::after {
  content: "✕";
}

@keyframes mmfadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes mmfadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes mmslideIn {
  from {
    transform: translateY(15%);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes mmslideOut {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-10%);
  }
}
.micromodal-slide {
  display: none;
}

.micromodal-slide.is-open {
  display: block;
}

.micromodal-slide[aria-hidden=false] .modal__overlay {
  animation: mmfadeIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden=false] .modal__container {
  animation: mmslideIn 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden=true] .modal__overlay {
  animation: mmfadeOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.micromodal-slide[aria-hidden=true] .modal__container {
  animation: mmslideOut 0.3s cubic-bezier(0, 0, 0.2, 1);
}

.modal__title {
  margin-bottom: 0.625rem;
}

.search {
  border: 1px solid rgba(51, 51, 51, 0.5);
  align-items: center;
  padding: 0.625rem;
}

.search > input {
  width: 100%;
}

.sidebar {
  display: none;
}
@media screen and (min-width: 768px) {
  .sidebar {
    display: block;
    flex: 1;
    background: rgb(245, 245, 245);
  }
}

ul.sidebar-nav {
  margin: 20px;
}

li.sidebar-list {
  display: block;
  box-shadow: 0rem 0.3125rem 0rem 0.0625rem rgba(192, 192, 192, 0.5);
  transition: 0.1s;
  border-radius: 0.3125rem;
}

li.sidebar-list:not(:last-child) {
  margin-bottom: 0.9375rem;
}

li.sidebar-list:hover {
  box-shadow: 0rem 0rem 0rem 0.0625rem rgba(192, 192, 192, 0.5);
  position: relative;
  transform: translateY(0.3125rem);
}

.sidebar-list > button {
  display: block;
  width: 100%;
  padding: 1.25rem 0;
}